'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import { ChevronLeft, ChevronRight, Play, Plus } from 'lucide-react'

export interface Anime {
  mal_id: number
  title: string
  synopsis?: string
  images: {
    jpg: {
      image_url: string
      small_image_url: string
      large_image_url: string
    }
  }
}

interface HeroCarouselProps {
  animes: Anime[]
  height?: string
  autoplayInterval?: number
}

export function HeroCarousel({
  animes,
  height = 'h-[20rem] md:h-[30rem]',
  autoplayInterval = 5000,
}: HeroCarouselProps) {
  const [current, setCurrent] = useState(0)
  const total = animes.length

  const nextSlide = () => setCurrent((current + 1) % total)
  const prevSlide = () => setCurrent((current - 1 + total) % total)

  useEffect(() => {
    if (total <= 1) return
    const timer = setInterval(() => nextSlide(), autoplayInterval)
    return () => clearInterval(timer)
  }, [current, total])

  if (total === 0) return null

  return (
    <div className={`relative w-full overflow-hidden rounded-xl ${height}`}>
      {animes.map((anime, idx) => {
        const isActive = idx === current
        return (
          <div
            key={anime.mal_id}
            className={`absolute inset-0 transition-all duration-1000 ease-in-out transform ${
              isActive ? 'opacity-100 scale-105 z-10' : 'opacity-0 scale-95 z-0'
            }`}
          >
            <Image
              src={
                anime.images?.jpg.large_image_url ||
                anime.images?.jpg.image_url ||
                anime.images?.jpg.small_image_url ||
                '/placeholder.jpg'
              }
              alt={anime.title}
              fill
              className="object-cover"
              priority={isActive}
            />

            <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent" />

            {/* Teks responsif, kecil di mobile */}
            <div className="absolute bottom-10 left-4 sm:left-6 md:bottom-12 md:left-12 max-w-[90%]">
              <h2 className="text-xl sm:text-2xl md:text-5xl font-bold mb-2 md:mb-3 line-clamp-1">
                {anime.title}
              </h2>

              {/* ðŸ”¥ DESKRIPSI AMAN â†’ dipendekkan via CSS, bukan JS */}
              <p className="text-xs sm:text-sm md:text-lg mb-3 md:mb-4 line-clamp-3 max-w-md">
                {anime.synopsis || 'No description.'}
              </p>

              <div className="flex gap-3 md:gap-4">
                <button className="flex items-center gap-2 bg-primary px-4 py-2 md:px-5 md:py-3 rounded hover:bg-primary/80 transition-colors text-xs sm:text-sm md:text-base">
                  <Play size={18} /> Watch
                </button>
                <button className="flex items-center gap-2 bg-white/20 px-4 py-2 md:px-5 md:py-3 rounded hover:bg-white/30 transition-colors text-xs sm:text-sm md:text-base">
                  <Plus size={18} /> Add to List
                </button>
              </div>
            </div>
          </div>
        )
      })}

      {total > 1 && (
        <>
          <button
            onClick={prevSlide}
            className="absolute left-3 top-1/2 -translate-y-1/2 bg-black/30 hover:bg-black/50 text-white p-3 md:p-4 rounded-full z-20"
          >
            <ChevronLeft size={28} className="md:w-8 md:h-8" />
          </button>

          <button
            onClick={nextSlide}
            className="absolute right-3 top-1/2 -translate-y-1/2 bg-black/30 hover:bg-black/50 text-white p-3 md:p-4 rounded-full z-20"
          >
            <ChevronRight size={28} className="md:w-8 md:h-8" />
          </button>
        </>
      )}

      <div className="absolute bottom-3 md:bottom-4 left-1/2 -translate-x-1/2 flex gap-2 z-20">
        {animes.map((_, idx) => (
          <span
            key={idx}
            className={`w-2 h-2 md:w-3 md:h-3 rounded-full transition-colors ${
              idx === current ? 'bg-white' : 'bg-white/50'
            }`}
          />
        ))}
      </div>
    </div>
  )
}
